 class MarvelService {
    _apiBase = 'https://gateway.marvel.com:443/v1/public/'
    _apiKey = 'apikey=0803ac54e9aa8cbbe036f1c7b3b3d1c0'
    _baseOffset = 210;
    getResoursse = async (url) =>{
         let res = await fetch(url);
            
            return await res.json();
     }

     getAllCharactres = async (offset = this._baseOffset) =>{
      const res =  await this.getResoursse(`${this._apiBase}characters?limit=9&offset=${offset}&${this._apiKey}`);
      return res.data.results.map(this._transformCharacter)
     }
//      getAllCharacters = async () => {
//       const res = await this.getResource(`${this._apiBase}characters?limit=9&offset=210&${this._apiKey}`);
//       return res.data.results.map(this._transformCharacter);
//   }

     getCharacter = async (id) =>{
       const res =  await this.getResoursse(`${this._apiBase}characters/${id}?${this._apiKey}`);
       return this._transformCharacter(res.data.results[0])
    }

    _transformCharacter = (char) =>{
      return {
         id: char.id,
         name: char.name,
         descr: char.description ? char.description.slice(0,220) + '...': 'description not found',
         homepage: char.urls[0].url,
         thumbnail: char.thumbnail.path + '.' + char.thumbnail.extension,
         wiki: char.urls[1].url,
         comics:  char.comics.items ? char.comics.items.slice(0,9) : 'comics not found'
      }

    }
 }

 export default MarvelService;